import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/Layout"
import PostExcerpt from "../components/PostExcerpt"
import SEO from "../components/SEO"
import ReactTags from 'react-tag-autocomplete'
import { getQueryVariable } from '../util'

import "../styles/post.scss"

export default function Blog({ location, data }) {

	const qPosts = process.env.NODE_ENV === "production" ? data.qPostsProduction : data.qPostsDevelopment

	const [tagSuggestions, setTagSuggestions] = React.useState([])
	const [posts, setPosts] = React.useState(qPosts.nodes)
	const [qTags, setQTags] = React.useState([])

	function objectify(tags) {
		return tags.map((tag, i) => { return {id: tag, name: tag} })
	}

	// populate suggestions
	React.useEffect(() => {
		const allTags = [...new Set(
					qPosts.nodes.reduce((sum, elem) => sum.concat(elem.frontmatter.tags), [])
				)]
		setTagSuggestions(objectify(allTags))
	}, [qPosts.nodes])

	// parse from URL
	React.useEffect(() => {
		const tagString = getQueryVariable('tags') || ''
		let tmp = tagString.split(',')
		if (tmp.length === 1 && tmp[0].length === 0)
			tmp = []
		setQTags(tmp)
	}, [location.search])

	// update post results to match qTags
	React.useEffect(() => {
		if (qTags.length > 0) {
			setPosts(qPosts.nodes.filter(node => qTags.every(tag => node.frontmatter.tags.includes(tag))))
			window.history.pushState('', '', `?tags=${qTags}`)
		}
		else {
			setPosts(qPosts.nodes)
			window.history.pushState('', '', '')
		}
	}, [qTags, qPosts.nodes])

	const reactTags = React.useRef(null)

	function onDelete(i) {
		const tags = qTags.slice(0)
    tags.splice(i, 1)
    setQTags(tags)
	}

	function onAddition(tag) {
		setQTags([].concat(qTags, tag.name))
	}

	return(
		<Layout>
			<SEO title="Blog"/>
			<div />
			<div className="content">
				<div className="post-title">Blog posts</div>
				<div>
					<ReactTags
						ref={reactTags}
						tags={objectify(qTags)}
						suggestions={tagSuggestions}
						onDelete={onDelete.bind(reactTags)}
						onAddition={onAddition.bind(reactTags)} 
						minQueryLength={1}
						placeholderText="Search by tags" />
				</div>

				{posts.map(node => {
					return(
						<PostExcerpt
							key={node.id}
							slug={node.fields.slug}
							frontmatter={node.frontmatter}
							excerpt={node.excerpt} />
					)
				})}
			</div>
		</Layout>
	)

}

export const postQuery = graphql`
	query allPosts {
		qPostsProduction: allMdx(filter: {fields: {collection: {eq: "posts"}}, frontmatter: {draft: {ne: true}}},
									 sort: { fields: [frontmatter___date], order: DESC }) {
			nodes {
				id
				excerpt(truncate: true)
				frontmatter {
					title
					date(formatString: "YYYY.MM.DD")
					tags
				}
				fields {
					slug
				}
			}
		}
		qPostsDevelopment: allMdx(filter: {fields: {collection: {eq: "posts"}}},
									 sort: { fields: [frontmatter___date], order: DESC }) {
			nodes {
				id
				excerpt(truncate: true)
				frontmatter {
					title
					date(formatString: "YYYY.MM.DD")
					tags
					draft
				}
				fields {
					slug
				}
			}
		}
	}
`