import React from "react"
import { Link } from "gatsby"
import PostDetails from "../components/PostDetails"

export default function PostExcerpt({id, slug, frontmatter, excerpt}) {

	return(
		<div className="index-post">
			<Link to={slug}>
				<span className="index-post-title">
					{frontmatter.draft && <span className="tag-draft">[Draft] </span>}{frontmatter.title}
				</span>
			</Link>
			<div className="post-details-in-index">
				<PostDetails frontmatter={frontmatter} showTags />
			</div>
			<p className="index-excerpt">{excerpt}</p>
		</div>
	)

}